import "core-js/modules/es.array.push.js";
import { formatDate } from '@/utils/chronos';
import { MediaTypes, ThumbnailFormats } from '@/utils/enums';
import { humanFileSize } from '@/utils/functions';
import AccessManager from '@/mixins/access-manager';
import SettingsMixin from '@/mixins/settings';
import ViewsBaseMixin from '@/mixins/views-base';
import { MEDIA_CONTEXT } from '@/store/modules/database/media';
import { mapActions } from 'vuex';
export default {
  name: 'Trash',
  mixins: [ViewsBaseMixin, AccessManager, SettingsMixin],
  data() {
    return {
      MediaTypes,
      mediaType: 0,
      mediaTypes: [],
      mediaSearch: '',
      isLoading: true,
      selectAll: false,
      currentItem: null,
      selectedMedias: [],
      currentItemIndex: 0,
      showDetailsDialog: false,
      enableMultiselect: false,
      isSelectAllIndeterminate: false,
      folder: 'medias',
      folderMedias: [],
      dbMedias: [],
      medias: []
    };
  },
  computed: {
    filteredMedias() {
      let searchFilterResult = [];
      const typeFilterResult = this.medias.filter(m => this.mediaType == 0 || m.type == this.mediaType);
      if (this.mediaSearch) {
        searchFilterResult = typeFilterResult.filter(m => m.name.toLowerCase().includes(this.mediaSearch.toLowerCase()));
      } else {
        searchFilterResult = typeFilterResult;
      }
      return searchFilterResult;
    }
  },
  created() {
    this.init();
    this.$_.forIn(MediaTypes, (value, key) => {
      this.mediaTypes.push(value);
    });
  },
  methods: {
    ...mapActions('media', ['getFolderMedias', 'deleteMediaFile', 'generateThumbnail']),
    async init() {
      const resp0 = await this.getAllItemsFromDB({
        context: MEDIA_CONTEXT
      });
      if (resp0.success && resp0.data) this.dbMedias = resp0.data;
      const resp1 = await this.getFolderMedias({
        folder: this.folder
      });
      if (resp1.success && resp1.data) this.folderMedias = resp1.data;
      this.folderMedias.forEach(file => {
        const isDeleted = this.dbMedias.findIndex(m => m.name == file.name) === -1;
        if (isDeleted) {
          this.medias.push({
            id: this.$uuidv4(),
            name: file.name,
            size: file.size,
            mime: file.mime,
            sender: this.userData.id,
            type: this.getFileTypeByName(file.name),
            link: '/root/' + this.folder + '/' + file.name,
            insert_datetime: formatDate(new Date(), {
              outPattern: 'YYYY-MM-DD hh:mm:ss',
              locale: this.$i18n.locale
            })
          });
        }
      });
      if (this.isLoading) this.isLoading = false;
    },
    getFileTypeByName(name) {
      const docExtList = ['DOC', 'DOCX', 'HTML', 'HTM', 'ODT', 'PDF', 'XLS', 'XLSX', 'ODS', 'PPT', 'PPTX', 'TXT'];
      const vidExtList = ['WEBM', 'MP4', 'AVI', 'MOV', 'FLV', 'OGG', 'WMV', 'MKV'];
      const imgExtList = ['JPEG', 'JPG', 'PNG', 'GIF', 'WEBP', 'SVG'];
      const audExtList = ['MP3', 'WAV', 'M4A', 'WMA', 'AAC', 'FLAC'];
      const fileExt = name.split('.').pop();
      if (fileExt) {
        if (docExtList.includes(fileExt.toUpperCase())) return MediaTypes.DOCUMENT.value;
        if (vidExtList.includes(fileExt.toUpperCase())) return MediaTypes.VIDEO.value;
        if (imgExtList.includes(fileExt.toUpperCase())) return MediaTypes.IMAGE.value;
        if (audExtList.includes(fileExt.toUpperCase())) return MediaTypes.AUDIO.value;
      }
      return MediaTypes.OTHERS.value;
    },
    showDetails(item, index) {
      this.currentItem = item;
      this.currentItemIndex = index;
      const type = this.mediaTypes.find(m => m.value == item.type);
      this.detailsData = [{
        name: this.$t('name'),
        value: item.name
      }, {
        name: this.$t('url'),
        value: this.resolveMediaLink(item.link)
      }, {
        name: this.$t('type'),
        value: type ? type.label : ''
      }, {
        name: this.$t('size'),
        value: humanFileSize(item.size, true)
      }, {
        name: this.$t('mimeType'),
        value: item.mime
      }];
      if (item.type == MediaTypes.IMAGE.value) {
        var img = new Image();
        img.onload = () => {
          const height = img.naturalHeight;
          const width = img.naturalWidth;
          if (width && height) {
            this.detailsData.push({
              name: this.$t('dimensions'),
              value: width + ' x ' + height
            });
          }
        };
        img.src = this.resolveMediaLink(item.link);
      }
      this.showDetailsDialog = true;
    },
    deleteItem(item, index) {
      this.$confirm(this.$t('deleteMediaPermanentlyQuestion'), this.$t('delete'), {
        confirmButtonText: this.$t('yes'),
        cancelButtonText: this.$t('cancel'),
        type: 'warning'
      }).then(async () => {
        const task = await this.deleteMediaFile({
          folder: this.folder,
          file: item.name
        });
        if (task.success) {
          this.$message.success(this.$t('success'));
          this.reset();
        } else {
          this.$message.error(this.$t('anErrorOccured'));
        }
      }).catch(() => {
        /* empty */
      });
    },
    deleteMultipleItems() {
      this.$confirm(this.selectedMedias.length === 1 ? this.$t('deleteMediaPermanentlyQuestion') : this.$t('deleteMultipleItems', [this.selectedMedias.length]), this.$t('delete'), {
        confirmButtonText: this.$t('yes'),
        cancelButtonText: this.$t('cancel'),
        type: 'warning'
      }).then(async () => {
        const mes = this.$message({
          message: this.$t('pleaseWait'),
          duration: 0
        });
        for (let i = 0; i < this.selectedMedias.length; i++) {
          await this.deleteMediaFile({
            folder: this.folder,
            file: this.selectedMedias[i].name
          });
        }
        mes.close();
        this.$message.success(this.$t('success'));
        this.reset();
      }).catch(() => {
        /* empty */
      });
    },
    restore(item, index) {
      this.$confirm(this.$t('restoreMediaQuestion'), this.$t('restore'), {
        confirmButtonText: this.$t('yes'),
        cancelButtonText: this.$t('cancel'),
        type: 'warning'
      }).then(async () => {
        const task = await this.addItemInDB({
          context: MEDIA_CONTEXT,
          data: {
            name: item.name,
            link: item.link,
            type: item.type,
            size: item.size,
            mime: item.mime,
            sender: this.userData.id
          }
        });
        if (task.success) {
          if (this.getFileTypeByName(item.name) == MediaTypes.IMAGE.value) {
            await this.generateThumbnail({
              autoWidth: this.thumbnailsFormat == ThumbnailFormats.AUTOWIDTH.value,
              autoHeight: this.thumbnailsFormat == ThumbnailFormats.AUTOHEIGHT.value,
              folder: this.folder,
              file: item.name,
              lib: this.imgProcessingLib,
              models: this.thumbnails.filter(thumbnail => thumbnail.label && thumbnail.width && thumbnail.height)
            });
          }
          this.$message.success(this.$t('success'));
          this.reset();
        } else {
          this.$message.error(this.$t('anErrorOccured'));
        }
      }).catch(() => {
        /* empty */
      });
    },
    restoreMultipleItems() {
      this.$confirm(this.selectedMedias.length === 1 ? this.$t('restoreMediaQuestion') : this.$t('restoreMultipleItems', [this.selectedMedias.length]), this.$t('restore'), {
        confirmButtonText: this.$t('yes'),
        cancelButtonText: this.$t('cancel'),
        type: 'warning'
      }).then(async () => {
        const mes = this.$message({
          message: this.$t('pleaseWait'),
          duration: 0
        });
        for (let i = 0; i < this.selectedMedias.length; i++) {
          await this.addItemInDB({
            context: MEDIA_CONTEXT,
            data: {
              name: this.selectedMedias[i].name,
              link: this.selectedMedias[i].link,
              type: this.selectedMedias[i].type,
              size: this.selectedMedias[i].size,
              mime: this.selectedMedias[i].mime,
              sender: this.userData.id
            }
          });
          if (this.getFileTypeByName(this.selectedMedias[i].name) == MediaTypes.IMAGE.value) {
            await this.generateThumbnail({
              autoWidth: this.thumbnailsFormat == ThumbnailFormats.AUTOWIDTH.value,
              autoHeight: this.thumbnailsFormat == ThumbnailFormats.AUTOHEIGHT.value,
              folder: this.folder,
              file: this.selectedMedias[i].name,
              lib: this.imgProcessingLib,
              models: this.thumbnails.filter(thumbnail => thumbnail.label && thumbnail.width && thumbnail.height)
            });
          }
        }
        mes.close();
        this.$message.success(this.$t('success'));
        this.reset();
      }).catch(() => {
        /* empty */
      });
    },
    updateSelectedMedias(item, index) {
      const i = this.selectedMedias.findIndex(m => m.id === item.id);
      if (i > -1) {
        if (this.selectedMedias.length == this.filteredMedias.length) this.isSelectAllIndeterminate = true;
        this.selectedMedias.splice(i, 1);
      } else this.selectedMedias.push(item);
    },
    handleSelectAll() {
      if (this.filteredMedias.length > this.selectedMedias.length) {
        this.selectedMedias = this.$_.cloneDeep(this.filteredMedias);
      } else {
        this.selectedMedias = [];
      }
      this.isSelectAllIndeterminate = false;
    },
    async reset() {
      this.isLoading = true;
      this.medias = [];
      this.selectedMedias = [];
      await this.init();
    }
  }
};